import {Component, OnInit} from '@angular/core';
import {FidjService} from 'fidj-angular';
import {ProfileService} from '../shared';
import {Router} from '@angular/router';
import {environment} from '../../environments/environment';
import {NgForm} from '@angular/forms';

@Component({
    selector: 'app-login',
    templateUrl: 'login.page.html',
    styleUrls: ['login.page.scss'],
})
export class LoginPage implements OnInit {
    public appVersion = environment.version;

    public userLoginEmail: string;

    public userLoginPassword: string;

    constructor(
        private router: Router,
        private profile: ProfileService,
        private fidjService: FidjService
    ) {}

    async ngOnInit() {
        if (this.fidjService.isLoggedIn()) {
            return this.router.navigateByUrl('/home');
        } else {
            console.warn('Not logged in');
        }

        this.userLoginEmail = this.profile.getEmail();
        await this.profile.logout(environment.fidjKey, environment.production);
    }

    async login(loginForm: NgForm) {
        if (!this.userLoginEmail || !loginForm.form.valid || !this.userLoginPassword) {
            return;
        }

        this.profile.setEmail(this.userLoginEmail);
        try {
            await this.fidjService.login(this.userLoginEmail, this.userLoginPassword);
            await this.router.navigate(['/home']);
        } catch (err) {
            alert(JSON.stringify(err));
        }
    }

    async loginInDemoMode() {
        this.profile.setEmail('demo user');
        this.profile.isDemoMode = true;
        try {
            await this.fidjService.loginInDemoMode();
            await this.router.navigate(['/home']);
        } catch (err) {
            alert(JSON.stringify(err));
        }
    }
}
